import { useQuery } from 'react-query';
import ProductCaraousel from '../components/productCaraousel';
import NewArrivals from '../components/newArrivals';
import Testimonials from '../components/testimonials';
import getCategories from '../services/products/getCategories';
import SkinCareRoutines from '../components/skinCareRoutines';
import CardContainerWithBackgroundImage from '../components/cardsContainerWithBackgorundImage';
import CardContainer from '../components/cardContainer';
import BrandImages from '../components/brandImages';
import Loading from '../components/loading';
import { Box } from '@material-ui/core';

const Home = () => {
  const { data, isLoading } = useQuery('categories', getCategories);

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div>
      <ProductCaraousel />
      <NewArrivals />

      <CardContainerWithBackgroundImage category={data[0]} />
      <CardContainer category={data[2]} />
      <CardContainer category={data[7]} />
      <CardContainerWithBackgroundImage category={data[6]} />
      <SkinCareRoutines />
      <Testimonials />
      <BrandImages />
      <Box display='flex' justifyContent='center'>
        <a href='https://rawasoftware.com/' target='blank'>
          <img src='/images/kk.png' alt='esewa-kbs' style={{ width: '50vw' }} />
        </a>
      </Box>
    </div>
  );
};

export default Home;
