import React from 'react';
import { Typography, Container } from '@material-ui/core';
import { useQuery } from 'react-query';
import about from '../services/kbs/about';
import Loading from '../components/loading';

const AboutUs = () => {
  const { isLoading, data } = useQuery('about', about);

  if (isLoading) {
    return <Loading />;
  }

  console.log(data);

  return (
    <Container>
      <Typography variant='h4' align='center' style={{ margin: '2rem 0' }}>
        ABOUT US
      </Typography>
      <Typography variant='h5' gutterBottom>
        WHO ARE WE ?
      </Typography>
      <Typography gutterBottom>{data[0].about_us}</Typography>
      <Typography variant='h5' gutterBottom>
        Delivery Charges
      </Typography>
      <Typography gutterBottom>{data[0].delivery_charges}</Typography>
      <Typography variant='h6' gutterBottom>
        {data[0].inside_valley}
      </Typography>
      <Typography variant='h6' gutterBottom>
        {data[0].outside_valley}
      </Typography>
      <Typography variant='h6' gutterBottom>
        Emergency Delivery: {data[0].emergency_delivery}
      </Typography>
      <Typography variant='h5' gutterBottom>
        Contact Us
      </Typography>
      <Typography gutterBottom>{data[0].contact_us}</Typography>
      <Typography gutterBottom variant='h6'>
        Phone Number: {data[0].phone_number}
      </Typography>
    </Container>
  );
};

export default AboutUs;
